import React from "react"
import Link from "gatsby-link"
import Helmet from "react-helmet"
// import "typeface-alegreya"
// import "typeface-alegreya-sans"
// import "typeface-raleway"
// import "typeface-roboto"
// import "typeface-ubuntu"
// import "typeface-fira-sans"

import "../pages/style.css"
import "../css/prism-coy.css"
import typography from "../utils/typography"

const rhythm = typography.rhythm
const scale = typography.scale


class Wrapper extends React.Component {
  render() {
    let header
    // Check if the location is either the front page or a tags page.
    // If so, use a big header, otherwise use a smaller one.
    if (
      ["/", "/tags/"].indexOf(this.props.location.pathname) !== -1 ||
      this.props.location.pathname.indexOf("/tags/") !== -1
    ) {
      header = (
        <Link
          style={{
            textDecoration: "none",
            boxShadow: "none",
            color: "grey",
          }}
          to="/"
        >
          <h1
            style={{
              ...scale(1),
              marginBottom: rhythm(0),
              marginTop: 0,
              color: "yellow,"
            }}
          >
            tech-stoic
          </h1>
        </Link>
      )
    } else {
      header = (
        <Link
          style={{
            textDecoration: "none",
            boxShadow: "none",
            color: "inherit",
          }}
          to="/"
        >
          <h3
            style={{
              marginTop: 0,
            }}
          >
            tech stoic
          </h3>
        </Link>
      )
    }
    return (
      <div
        style={{
          padding: `${rhythm(1.5)} ${rhythm(3 / 4)}`,
          maxWidth: rhythm(22),
          margin: `0 auto`,
        }}
      >
        <Helmet defaultTitle="tech-stoic" titleTemplate="tech-stoic | %s" />
        <div>{header}
        </div>
        {this.props.children}
      </div>
    )
  }
}

export default Wrapper
